.buttonsContainer {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 21px;
}

.buttonsContainer button {
    min-width: 84px;
}

.clock {
    position: relative;
    margin-bottom: 168px;
}

.watch {
    font-family: Montserrat, sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    margin-bottom: 55px;
}

.watch strong {
    font-weight: 600;
}

.more {
    position: absolute;
    left: 0;
    top: 32px;
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    color: rgba(0, 0, 0, 0.5);
}