.item {
    border-bottom: 1px solid #E5E5E5;
}

.users {
    width: 100%;
    border-collapse: collapse;
}

.buttonsContainer {
    margin-bottom: 18px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 12px;
}

.container {
    margin-bottom: 40px;
}

.thead {
    width: 100%;
    background-color: #E5E5E5;
}

.thead .tr {
    width: 100%;
}

.nameCol, .ageCol {
    padding: 16px;
    width: 50%;
    font-family: Roboto, sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0;
    text-align: left;
}

.thead .nameCol,
.thead .ageCol {
    font-weight: 600;
}
