.radio {
    display: flex;
    align-items: center;
    flex-direction: row;
    position: relative;
}

input[type="radio"]{
    visibility:hidden;

}

.radio::before {
    visibility: visible;
    position: absolute;
    left: -15px;
    top: -5px;
    content: '';
    width: 20px;
    height: 20px;
    flex: 0 0 20px;
    border-radius: 50%;
    border: 2px solid black;
    cursor: pointer;
}


.radio:checked::after {
    visibility: visible;
    position: absolute;
    left: -10px;
    top: 0px;
    content: '';
    width: 14px;
    height: 14px;
    flex: 0 0 14px;
    border-radius: 50%;
    background-color: black;
}

.label {
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-bottom: 22px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
    cursor: pointer;
}

